import React, { useEffect, useRef, useState } from "react"
import PropTypes from "prop-types"
import FullPageSnap from "react-full-page-snap"
import HomeHeader from "../views/home/HomeHeader"
import IconsCloud from "../views/skills/IconsCloud"
import Project from "../views/projects/Project"

const DesktopSite = props => {
  const [currentPage, setCurrentPage] = useState(0)
  const [isSecondPage, setIsSecondPage] = useState(false)

  const { projects } = props

  const ref = useRef(null)

  // useEffect(() => console.log(projects.edges.map(p => p.node)))

  useEffect(() => {
    if (!isSecondPage && currentPage === 1) setIsSecondPage(true)
  }, [currentPage])

  return (
    <FullPageSnap
      ref={ref}
      beforePageChange={(o, p) => setCurrentPage(p)}
      scrollOptions={{ speed: 1000 }}
    >
      <HomeHeader
        pageDownCallback={() => {
          if (ref.current) ref.current.goToPage(1)
        }}
      />
      <IconsCloud expanded={isSecondPage} size={300} />
      {projects.edges.map(p => (
        <Project key={p.node.title} {...p.node} />
      ))}
    </FullPageSnap>
  )
}

const horizontalShape = PropTypes.shape({
  aspectRatio: PropTypes.number.isRequired,
  base64: PropTypes.string.isRequired,
  sizes: PropTypes.string.isRequired,
  src: PropTypes.string.isRequired,
  srcSet: PropTypes.string.isRequired,
})

DesktopSite.propTypes = {
  projects: PropTypes.shape({
    edges: PropTypes.arrayOf(
      PropTypes.shape({
        node: PropTypes.shape({
          description: PropTypes.string.isRequired,
          flags: PropTypes.arrayOf(PropTypes.string.isRequired).isRequired,
          preview: PropTypes.shape({
            childImageSharp: PropTypes.shape({
              horizontal: horizontalShape.isRequired,
              vertical: horizontalShape.isRequired,
            }).isRequired,
          }).isRequired,
          title: PropTypes.string.isRequired,
          url: PropTypes.string.isRequired,
        }).isRequired,
      }).isRequired
    ).isRequired,
  }).isRequired,
}

export default DesktopSite
