import React from "react"
import { ThemeProvider } from "styled-components"
import { isMobileOnly } from "react-device-detect"
import { graphql, useStaticQuery } from "gatsby"
import { Helmet } from "react-helmet"
import MobileSite from "../browsers/MobileSite"
import DesktopSite from "../browsers/DesktopSite"
// import {useWindowSize} from "../core/hooks";

const darkTheme = {
  colors: {
    primary: "#fff",
    accent: "#f55600",

    label: "#fff",
    secondaryLabel: "#2b2b2b",

    systemBackground: "#212121",
  },
}
const IndexPage = () => {
  // const [width, height] = useWindowSize();
  // console.log([width, height])
  const { allProjectsYaml } = useStaticQuery(graphql`
    query ProjectsData {
      allProjectsYaml {
        edges {
          node {
            title
            flags
            url
            preview {
              childImageSharp {
                horizontal: fluid(maxWidth: 800) {
                  ...GatsbyImageSharpFluid
                }
                vertical: fluid(maxWidth: 400) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            description
          }
        }
      }
    }
  `)
  return (
    <ThemeProvider theme={darkTheme}>
      <Helmet>
        <meta
          name="viewport"
          content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no"
        />
      </Helmet>
      {!isMobileOnly ? (
        <DesktopSite projects={allProjectsYaml} />
      ) : (
        <MobileSite projects={allProjectsYaml} />
      )}
      {/* <MobileOnlyView> */}
      {/*    <MobileSite projects={allProjectsYaml}/> */}
      {/* </MobileOnlyView> */}
      {/* <BrowserView> */}
      {/*    <DesktopSite projects={allProjectsYaml}/> */}
      {/* </BrowserView> */}
    </ThemeProvider>
  )
}

export default IndexPage
