import React from "react"
import styled from "styled-components"
import PropTypes from "prop-types"
import Img from "gatsby-image"
import GoToButton from "./components/GoToButton"
import devices from "../../core/devices"

const ProjectContainer = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
  padding: 20px;
  color: ${props => props.theme.colors.label};
`

/* const PreviewImage = styled.figure`
  display: flex;
  align-items: center;
  flex: 1;
  min-width: 300px;
  max-width: 700px;
  min-height: 150px;
  background-image: url("${props => props.url}");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;

  @media (min-width: ${devices.laptop}) {
    min-height: 250px;
    min-width: 500px;
  }
`; */

const ProjectImgPreview = styled.div`
  margin: auto 0;
  width: 90vw;
  height: 250px;

  @media (min-width: ${devices.tablet}) {
    width: 50vw;
    height: 50vh;
  }
`

const ProjectCaption = styled.figcaption`
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-around;
  width: 300px;
  min-height: 500px;
`

const CaptionText = styled.div`
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  height: fit-content;
`

const CaptionTitle = styled.h2`
  margin: 5px 0;
`

const CaptionFlagsContainer = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 10px 0;
`

const CaptionFlag = styled.div`
  padding: 4px 6px;
  margin: 4px;
  border: ${props => props.theme.colors.accent} 1px solid;
  border-radius: 5px;
  color: ${props => props.theme.colors.accent};
  font-size: 15px;
`

const Project = props => {
  const { preview, title, flags, description, url, mobile } = props

  return (
    <ProjectContainer>
      <ProjectImgPreview>
        <Img
          fluid={
            mobile
              ? preview.childImageSharp.vertical
              : preview.childImageSharp.horizontal
          }
          alt="Gatsby Docs are awesome"
          style={{ height: "100%", width: "100%" }}
          imgStyle={{ objectFit: "contain" }}
        />
      </ProjectImgPreview>
      <ProjectCaption>
        <CaptionText>
          <CaptionTitle>{title}</CaptionTitle>
          <CaptionFlagsContainer>
            {flags.map(p => (
              <CaptionFlag key={p.substring(0, 4)}>{p}</CaptionFlag>
            ))}
          </CaptionFlagsContainer>
          <div>{description}</div>
          <GoToButton url={url} />
        </CaptionText>
      </ProjectCaption>
    </ProjectContainer>
  )
}

Project.propTypes = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  // eslint-disable-next-line react/forbid-prop-types
  flags: PropTypes.array.isRequired,
  // eslint-disable-next-line react/forbid-prop-types
  preview: PropTypes.any.isRequired,
  url: PropTypes.string.isRequired,
  mobile: PropTypes.bool,
}

Project.defaultProps = {
  mobile: false,
}

export default Project
